[class*='miniprofile_miniProfileContent'] {
  /* mini profile Background */
  &,
  [class*='miniprofile_miniProfileHeader'],
  [class*='miniprofile_miniProfileBottom'] {
    background: rgb(var(--header)) !important;
  }

  /* mini profile Blur */
  [class*='miniprofile_miniProfileVideoBackgroundContainer'],
  [class*='miniprofile_miniProfileBackgroundBlur'] {
    display: none;
  }
}
